<template>
    <div>
        <Input style="width: 200px;margin-right:30px;" v-model="tagName"></Input>
        <Button @click="addTag()" type="primary">新增标签</Button>
        <Table class="tag-table" stripe :columns="tagHeader" :data="tagList"></Table>
    </div>
</template>

<script>
import Axios from "axios";
export default {
  data() {
    return {
      tagList: [],
      tagName: "",
      tagHeader: [
        {
          key: "_id",
          title: "序号",
          align: "center"
        },
        {
          key: "name",
          title: "标签名称",
          align: "center"
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h(
              "Button",
              {
                props: {
                  type: "error",
                  size: "small"
                },
                on: {
                  click() {
                    let id = params.row._id;
                    that.$Modal.confirm({
                      title: "提示:",
                      content: "确认删除这条内容吗？",
                      onOk: () => {
                        that.$axios.delete("/tag/" + id).then(res => {
                          that.$Message.info("删除成功");
                          that.showTagList();
                        });
                      }
                    });
                  }
                }
              },
              "删除"
            );
          }
        }
      ]
    };
  },
  mounted() {
    this.showTagList();
  },
  methods: {
    showTagList() {
      this.$axios.get("/tag").then(res => {
        this.tagList = res.data.data;
      });
    },
    addTag() {
      this.$axios.post("/tag", { name: this.tagName }).then(res => {
        this.$Message.info("添加成功");
        this.showTagList();
      });
    }
  }
};
</script>

<style scoped>
.tag-table {
  margin-top: 30px;
}
</style>
